import React from "react";
import { personalData } from "./personalData";
import "./personalData.css";

function App() {
  return (
    <>
      <section>
        <article id={personalData.id}>
          <h1>First Name is {personalData.firstName}</h1>
          <h1>Last Name is {personalData.lastName}</h1>
          <h1>I am {personalData.age} years old</h1>
          <h1>The current year is {personalData.currentYear}</h1>
        </article>
      </section>
    </>
  );
}

export default App;
